import { doc, setDoc, getDoc } from "firebase/firestore";
import { User as FirebaseUser } from "firebase/auth";
import { db } from "../firebase";
import { cleanFirestoreData } from "./clean-data";

export interface User {
  displayName: string;
  email: string;
  photoURL?: string;
  phone?: string;
  labName?: string;
  labAddress?: string;
  labContacts?: string[];
}

export const getUser = async (uid: string): Promise<User | null> => {
  const userDoc = await getDoc(doc(db, "users", uid));

  if (!userDoc.exists()) {
    return null;
  }

  return userDoc.data() as User;
};

export const createUser = async (
  uid: string,
  firebaseUser: FirebaseUser
): Promise<void> => {
  await setDoc(doc(db, "users", uid), {
    displayName: firebaseUser.displayName ?? "",
    email: firebaseUser.email ?? "",
    photoURL: firebaseUser.photoURL ?? null,
    phone: firebaseUser.phoneNumber ?? null,
  });
};

export const updateUser = async (
  uid: string,
  userData: Partial<User>
): Promise<void> => {
  const cleanedData = cleanFirestoreData(userData, {
    optionalStringFields: ["phone", "photoURL", "labName", "labAddress"],
    arrayFields: ["labContacts"],
  });

  await setDoc(doc(db, "users", uid), cleanedData, { merge: true });
};
